{/* 右侧：ATS-Friendly A4 预览 */}
<div className="w-1/2 bg-zinc-200 p-8 flex justify-center overflow-y-auto">
  <div 
    id="resume-preview" 
    className="w-[210mm] min-h-[297mm] h-fit bg-white p-[1in] shadow-2xl flex flex-col font-serif text-[#111] leading-tight"
  >
    {/* Header: 美国习惯姓名居中，不放照片 */}
    <div className="text-center border-b-[1.5px] border-black pb-4 mb-6">
      <h1 className="text-3xl font-bold uppercase tracking-tight">
        {resume.personalInfo.fullName || "YOUR NAME"}
      </h1>
      <div className="text-[11pt] mt-2 flex justify-center gap-3">
        <span>{resume.personalInfo.email}</span>
        {resume.personalInfo.email && <span>•</span>}
        <span>LinkedIn.com/in/username</span>
        <span>•</span>
        <span>Github.com/handle</span>
      </div>
    </div>

    {/* Section: Experience (最核心部分) */}
    <div>
      <h3 className="text-[13pt] font-bold border-b border-gray-400 mb-3 uppercase">Experience</h3>
      {resume.experience.map((exp: any, i: number) => (
        <div key={i} className="mb-4">
          <div className="flex justify-between items-baseline font-bold text-[11pt]">
            <span>{exp.company || "COMPANY NAME"}</span>
            <span className="font-normal italic">San Francisco, CA</span>
          </div>
          <div className="flex justify-between items-baseline text-[10.5pt] mb-1 italic">
            <span>{exp.role || "Software Engineer Intern"}</span>
            <span className="font-normal not-italic text-[10pt]">May 2024 – Aug 2024</span>
          </div>
          {/* 渲染量化后的 Bullet Points */}
          <div className="text-[10pt] text-gray-800 space-y-1 ml-4">
             {exp.description.split('\n').map((line: string, idx: number) => (
               <p key={idx} className="relative before:content-['•'] before:absolute before:-left-4">
                 {line.replace(/^•\s*/, '')}
               </p>
             ))}
          </div>
        </div>
      ))}
    </div>

    {/* Section: Education (留学生必填) */}
    <div className="mt-4">
      <h3 className="text-[13pt] font-bold border-b border-gray-400 mb-2 uppercase">Education</h3>
      <div className="flex justify-between font-bold text-[11pt]">
        <span>University of Southern California</span>
        <span className="font-normal italic">Los Angeles, CA</span>
      </div>
      <div className="flex justify-between text-[10.5pt] italic">
        <span>Master of Science in Computer Science</span>
        <span className="font-normal not-italic">Expected May 2025</span>
      </div>
    </div>
  </div>
</div>